:root {
  --column-count: 1;
}

html {
  font-family: Roboto, Arial, sans-serif;
}

body {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

.header {
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
}

.header .logo {
  object-fit: contain;
  width: 40px;
  height: 40px;
  padding: 10px 0;
}

.header nav {
  text-transform: uppercase;
}

.header nav .nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header nav .nav__list .nav_item {
  color: #060b35;
  transition: all .15s ease-in-out;
}

.header nav .nav__list .nav_item:not(:last-child) {
  margin-right: 20px;
}

.header nav .nav__list .nav_item:hover {
  color: #00acdc;
}

.header nav .nav__list .nav_item .nav_link {
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  display: inline-block;
}

.header nav .nav__list .nav_item .is-active {
  color: #00acdc;
  position: relative;
}

.header nav .nav__list .nav_item .is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.cards__container {
  grid-template-rows: 1fr 1fr;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .cards__container {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .cards__container {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .cards__container {
    --column-count: 4;
  }
}

.product__card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  transition: all .3s ease-in-out;
  display: flex;
}

.product__card:hover {
  transform: scale(1.2);
}

.product__card:hover .product__name {
  color: #34568b;
}

.product__card .product__image {
  background: url("imac.202c52ff.jpeg") center / contain no-repeat;
  height: 134px;
  margin: 32px 20px 40px;
}

.product__card .product__info {
  flex-direction: column;
  padding: 0 16px 16px;
  display: flex;
}

.product__card .product__info .product__name {
  padding-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product__card .product__info .product__code {
  color: #616070;
  padding-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product__card .product__info .product__review-box {
  justify-content: space-between;
  width: 100%;
  padding-bottom: 24px;
  display: flex;
}

.product__card .product__info .product__review-box .product__stars {
  display: flex;
}

.product__card .product__info .product__review-box .product__stars .product__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 16px;
  height: 16px;
}

.product__card .product__info .product__review-box .product__stars .product__star:not(:last-child) {
  margin-right: 4px;
}

.product__card .product__info .product__review-box .product__stars .product__star--highlighted {
  background-image: url("star-active.edda4c43.svg");
}

.product__card .product__info .product__review-box .product__review-counter {
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product__card .product__info .product__price-box {
  justify-content: space-between;
  width: 100%;
  padding-bottom: 16px;
  display: flex;
}

.product__card .product__info .product__price-box .product__price-label {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product__card .product__info .product__price-box .product__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product__card .product__info .buy-button {
  box-sizing: border-box;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  padding: 12px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .15s ease-in-out;
}

.product__card .product__info .buy-button:hover {
  color: #00acdc;
  background-color: #fff;
}
